<script>
  export default {
    data() {
      return {
        data: [{
          label: '1',
          children: [{
            label: '1'
          }]
        }, {
          label: '2',
          children: [{
            label: '2'
          }, {
            label: '3'
          }]
        }, {
          label: '3',
          children: [{
            label: '4',
            children: [{
              label: '6'
            }]
          }, {
            label: '4'
          }, {
            label: '5'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        regions: [{
          'name': '/root'
        }, {
          'name': '/home'
        }],
        props: {
          label: 'name',
          children: 'zones'
        },
        count: 1
      }
    },
    methods: {
      handleCheckChange(data, checked, indeterminate) {
        console.log(data, checked, indeterminate)
      },
      handleNodeClick(data) {
        console.log(data)
      },
       loadNode(node, resolve) {
        if (node.level === 0) {
          return resolve([{ name: '/root' }, { name: '/home' }])
        }
        if (node.level > 3) return resolve([])

        var hasChild
        if (node.data.name === '/root') {
          hasChild = true
        } else if (node.data.name === '/home') {
          hasChild = false
        } else {
          hasChild = Math.random() > 0.5
        }

        setTimeout(() => {
          var data
          if (hasChild) {
            data = [{
              name: '' + this.count++
            }, {
              name: '' + this.count++
            }]
          } else {
            data = []
          }

          resolve(data)
        }, 500)
      }
    }
  }
</script>


